/* reset */
$min-width: 320px; 
$max-width: 1200px; 
$base-bg: #FFF; 
$base-font-size: 13px; 
$base-line-height: 18px; 
$base-font-family: Arial, sans-serif; 
$custom-font-family: '"Gilroy-Light"', sans-serif;
$custom-font-family_: 'Open Sans Regular', sans-serif;
$custom-font-family__: 'Comfortaa-Bold', sans-serif;
$link-color: #100da9;
$default-transition:all 0.2s linear;

/* main variables */

//text, titles
$text-color: #000;
$title-color: #000;
$title-font-family: $custom-font-family;
$grey: #c7c7c7;
$black: #231e1e;

$font-size-h1: 36px;
$line-height-h1: 42px;
$font-size-h2: 32px;
$line-height-h2: 37px;
$font-size-h3: 28px;
$line-height-h3: 32px;


.but1, .but2{
  border: none;
  padding: 10px 40px;
  font-size: 18px;
  border-radius: 5px;
  font-family: Gilroy-Bold, sans-serif;
  margin-bottom: 10px;
}

.but1{
  background: $black;
  color: white;
  text-decoration: none;
  &:hover{
    background: grey;
    color: $black;
  }
}
.but2{
  background: white;
  color: $black;
  border: 1px solid $black;
  &:hover{
    background: grey;
    color: white;
  }
}
.icons{
  display: grid;
  grid-template-columns: 50px 50px 50px;
  align-items: end;
  grid-column-gap: 15px;
  .icons-item:nth-of-type(1){
    background: url("../../img/sprite_.png") -103px 205px;
    width: 49px;
    height: 49px;
  }
  .icons-item:nth-of-type(2){
    background: url("../../img/sprite_.png") -158px 205px;
    width: 49px;
    height: 49px;
  }
  .icons-item:nth-of-type(3){
    background: url("../../img/sprite_.png") -210px 205px;
    width: 49px;
    height: 49px;
  }
}

.popup {
  display: none;
}
input{
  display: none;
}
.popup {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.slick-next {
  right: 300px;
  top:40%;

  &:before{
    content: '';
    position: absolute;
    background: url(../../img/sl.png) -309px 232px;
    width: 53px;
    height: 50px;
  }
}
.slick-dots li button{
  font-size: 0;
  line-height: 0;
  display: block;
  width: 10px;
  height: 10px;
  padding: 5px;
  border: 1px solid black;
  background: black;
  border-radius: 50%;
  cursor: pointer;
  color: transparent;
}
.slick-dots li button:before {
  font-family: 'slick';
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  border: 0;
  outline: none;
  background: transparent;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  content: '';
  text-align: center;
  opacity: .25;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots {
  bottom: 63px;
}
.popup:before {
  display: block;
  content: ' ';
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: .5;
}

.popup-content {
  width: 940px;
  height: 580px;
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background-color: #fff;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  text-align: center;
}
.popup-input{
  border-top: 2px solid $grey;
  border-right: 2px solid $grey;
  border-bottom: 2px solid $grey;
  border-bottom-style: outset;
  padding: 45px 65px;
  width: 100%;
  font-family: "Gilroy-Light", sans-serif;
  h2{
    font-size: 36px;
    font-weight: bold;
    padding-bottom: 28px;
  }
  input, textarea{
    display: block;
    width: 100%;
    height: 50px;
    border-radius: 5px;
    border: 1px solid $grey;
    margin-bottom: 15px;
    color: $grey;
    font-size: 20px;
    padding-left: 30px;
  }
  textarea{
    height: 145px;
    padding-top: 15px;
    margin-bottom: 25px;
  }
}
.popup-img{

}

.popup-shower {
  color: #00f;
  cursor: pointer;
  text-decoration: none;
}

.popup-shower:hover {
  color: #00a;
  text-decoration: none;
}

.popup-closer {
  position: absolute;
  top: 5px;
  right: 5px;
  color: $black;
  font-size: 20px;
  display: block;
  width: 25px;
  height: 25px;
  line-height: 20px;
  text-align: center;
  cursor: pointer;
}

.popup-closer:hover {
  background-color: #eee;
}
.popup-checkbox:checked + .popup {
  opacity: 1;
  display: block;
}
@media screen and (max-width: 1050px){
  .popup-content{
    width: 600px;
  }
  .popup-img{
    display: none;
  }
}
@media screen and (max-width: 768px){
  .popup-content{
    width: 300px;
  }
  .popup-input{
    padding: 5px 5px;
    }
}