@import '../utils/_mixins';
@import '../utils/_variables';
//main styles

.main-wrapper {
	padding: 0 0 0 0;
	min-width: $min-width;
	width: 100%;
	position: relative;
	overflow: hidden;
	min-height: 100%;
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	flex-direction: column;
	-webkit-flex-direction: column;
	-ms-flex-direction: column;
	justify-content: flex-start;
	-webkit-justify-content: flex-start;
	-ms-flex-pack: start;
}

.wrapper {
	min-width: $min-width;
	max-width: $max-width;
	padding: 0 15px 0 15px;
	margin: 0 auto;
	position: relative;
}

/* titles */
p {
	@include font($base-font-size, $base-line-height, $text-color);
	padding: 15px 0 15px 0;
	font-weight: 400;
}

h1 {
	@include font($font-size-h1, $line-height-h1, $title-color);
	font-family: $custom-font-family__;
	padding: 15px 0 15px 0;
	font-weight: 400;
}

h2 {
	@include font($font-size-h2, $line-height-h2, $title-color);
	font-family: $title-font-family;
	padding: 15px 0 15px 0;
	font-weight: 400;
}

h3 {
	@include font($font-size-h3, $line-height-h3, $title-color);
	font-family: $title-font-family;
	padding: 15px 0 15px 0;
	font-weight: 400;
}

/* text position */
.text-left {
	text-align: left !important
}

.text-center {
	text-align: center !important
}

.text-right {
	text-align: right !important
}

.nowrap {
	white-space: nowrap !important;
}

/* loader */
.loaded .main-wrapper {
	visibility: hidden;
}

.icon-load {
	background: url(../img/loader.gif) no-repeat left top;
	width: 40px;
	height: 40px;
	position: fixed;
	left: 50%;
	top: 50%;
	margin-left: -20px;
	margin-left: -20px;
	display: none;
}

.loaded .icon-load {
	display: block;
}

