.slider-block{
	overflow: hidden;
	position: relative;
	font-family: "Gilroy-Light", sans-serif;
	.slider-info{
		padding-top: 65px;
		text-align: center;
		background: #fafafa;
		h6{
			font: {
				size: 36px;
				family: Gilroy-Bold, sans-serif;
			};
			padding-bottom: 35px;
		}
		.buttons{
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			.margin-r{
				margin-right: 25px;
				margin-bottom: 35px;
			}

		}
	}
	.slider_{
		padding-bottom: 137px;
		border-bottom: 2px solid $grey;
	}
	.slick-slide{
		padding-right: 70px;
	}
	.slider-grid{
		overflow: hidden;
		background: #fafafa;
	margin-left: -120px;
		width: 100%;
		display: grid;
		grid-template-columns: minmax(400px, 880px) 320px 335px;
		grid-template-rows:minmax(474px, auto)   minmax(205px, auto)  ;


		grid-template-areas: "a b b"
		   					 "a k f";
		.slider-photo{
			grid-area: a;
			background-size: cover;
			background-position:center;
			img{
				width: 100%;
			}
		}

		.slider-info{
			grid-area: b;
			overflow: hidden;
			padding: 30px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			border-top: 2px solid $grey;
			border-right: 2px solid $grey;
			border-bottom: 2px solid $grey;
			h6{
				font: {
					size: 30px;
					weight: bold;
				};
			}
			.slider-buttons{
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				.but-item1{
					margin-right: 30px;
				}
			}
		}
		.slider-data{
			padding: 30px;
			grid-area: k;
			display: flex;
			justify-content: center;
			border-right: 2px solid $grey;
			border-bottom: 2px solid $grey;
			text-transform: uppercase;
			font-family: Gilroy-Medium, sans-serif;
			padding-right: 2px;
			padding-left: 2px;
			p{
				font-size: 18px;
				display: flex;
				align-items: center;
				&:before{
					content: '';
					background: url(../../img/sprite_.png) -258px 198px;
					width: 60px;
					height: 38px;
					display: inline-block;
				}
			}
		}
		.slider-ad{
			grid-area: f;
			padding: 30px;
			display: flex;
			justify-content: center;
			border-right: 2px solid $grey;
			border-bottom: 2px solid $grey;
			text-transform: uppercase;
			//padding-right: 2px;
			//padding-left: 2px;
			font-family: Gilroy-Bold, sans-serif;
			p{
				font-size: 18px;
				display: flex;
				font-weight: bold;
				align-items: center;
				&:before{
					content: '';
					background: url(../../img/sprite_.png) -57px 196px;
					width: 52px;
					height: 38px;
					display: inline-block;
				}
			}
		}
		.slider-empty{
			grid-area: d;
		}
	}

}
.about{
	text-align: center;

	h6{
		display: inline-block;
		font-size: 36px;
		font-family: Gilroy-Bold, sans-serif;
		padding-top: 65px;
		padding-bottom: 35px;
	}
	.about-img
	{
		background: url("../../img/photo-3.png") round;
		width: 100%;
		overflow: hidden;
		padding: 0 320px 70px 320px;
	}
	.about-block{
		display: grid;
		grid-template-columns: 57% 43%;
		grid-template-rows: 430px 250px;
		grid-template-areas:
						"a b"
						"a k";
		.block-item1{
			grid-area: a;
			background: white;
			border-top: 2px solid $grey;
			border-right: 2px solid $grey;
			text-align: left;
			padding: 44px 85px 10px 85px;
			font-family: "Gilroy-Light", sans-serif;
			strong{
				font-size: 20px;
				font-family: Gilroy-Bold, sans-serif;
				line-height: 25px;
			}
			p{
				font-size: 20px;
				padding-top: 5px;
				padding-bottom: 5px;
			}
		}
		.block-item2{
			grid-area: b;
			background: white;
			border-top: 2px solid $grey;
			text-align: left;
			padding: 44px 84px 10px 85px;
			font-family: "Gilroy-Light", sans-serif;
			strong{
				font-size: 20px;
				font-family: Gilroy-Bold, sans-serif;
				line-height: 1px;
			}
			p{
				font-size: 20px;
				padding-top: 0;
				padding-bottom: 0;
				&:before{
					content: '';
					background: url(../../img/sprite2.png) -430px 199px;
					width: 49px;
					height: 35px;
					margin-left: -50px;
					display: inline-block;
				}
			}
		}
		.block-item3{
			grid-area: k;
			background: white;
			border-top: 2px solid $grey;
			font-family: "Gilroy-Light", sans-serif;
			text-align: left;
			padding: 44px 48px 10px 48px;
			strong{
				font-size: 20px;
				font-family: Gilroy-Bold, sans-serif;
			}
			p{
				font-size: 20px;
				padding-top: 5px;
				padding-bottom: 20px;
			}
		}
	}
}
.advantages{
	font-family: "Gilroy-Light", sans-serif;
	border-bottom: 2px solid $grey;
	border-top: 2px solid $grey;
	position: relative;
	.advantages-titles{
		text-align: center;
		padding-top: 70px;
		padding-bottom: 40px;
		h6{
			font-size: 36px;
			font-family: Gilroy-Bold, sans-serif;
		}
	}
	.advantages-blocks{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		text-align: center;
		.advantages-block{
			margin-bottom: 50px;
			width: 305px;
			border: 2px solid $grey;
			padding: 45px 25px;
			.advantages-img{
				padding-bottom: 20px;
			}
			strong{
				font-size: 18px;
				font-family: Gilroy-Bold, sans-serif;
				padding-bottom: 10px;
				display: inline-block;
				text-transform: uppercase;
			}
			p{
				font-size: 18px;
				font-family: Gilroy-Regular, sans-serif;
				padding: 0;
			}
		}
	}
}
.slick-list{
	padding:0 120px !important;
}
@media screen and (max-width: 1800px) {
	.about .about-img{
		padding: 0 70px 50px 70px;
	}
}
@media screen and (max-width: 1430px) {
	.slider-block .slider_ .slider-grid {
		grid-template-columns:  335px 1fr;
		grid-template-rows:  minmax(300px, auto)  minmax(400px, auto) ;
		grid-template-areas: "k b"
		                     "f a";
	}
	.slider-block .slider-grid .slider-data {

		border-left: 2px solid #c7c7c7;
		border-top: 2px solid #c7c7c7;

	}
	.slider-block .slider-grid .slider-ad {
		border-left: 2px solid #c7c7c7;
		border-top: 2px solid #c7c7c7;
	}
	.slider-block .slider-grid .slider-info .slider-buttons {
	flex-direction: column;
	}
	.slider-block .slider-grid .slider-info .slider-buttons .but-item1 {
		margin:10px 0;
	}
	.slick-next{
		right: 15.5%;
	}
}
@media screen and (max-width: 1300px){
	.about .about-block{
		grid-template-columns: 100%;
		grid-template-rows: auto auto auto;
		grid-template-areas:
						"a"
