@import '../utils/_mixins';
@import '../utils/_variables';
/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header{
  display: grid;
  grid-template-columns: 16.6% 18.75% 18.75% 45.85%;
  grid-template-rows: 90px 625px 240px;
  grid-template-areas:
          "a b b b"
          "a c c d"
          "a e f d";
  font-family: "Gilroy-Light", sans-serif;
  .name{
    grid-area: a;
    border-right: 2px solid $grey;
    border-bottom: 2px solid $grey;
    text-align: center;
    justify-content: center;
    display: grid;
    padding: 35px 5px;
    .ars{
      width: 160px;
      display: inline-block;
      text-decoration: none;
      color: black;
      .title{
        font-size: 48px;
      }
      .name-text{
        font-size: 16px;
      }
    }

  }
  input[type=checkbox]{
    display: none;
  }
  #nav__:checked ~ .menu > .nav-button{
  display: block;
  }
  #nav__:checked ~ .menu > nav > ul{
    display: block;
    max-height: 300px;
    padding-bottom: 10px;
    margin-left: 0;
    position: absolute;
    background: rgba(42,42,42, .9);
  }
  .menu{
    grid-area: b;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 35px 0;
    z-index: 1;
    nav{
      height: 100%;
      ul{
        display: flex;
        margin-left: 78px;
        text-decoration: none;
        align-items: center;
        height: 100%;
        li{
          display: flex;
          list-style-type: none;
          margin-right: 40px;
          a{
            font-size: 20px;
            color: black;
            font-family: Gilroy-Medium, sans-serif;
            font-weight: 500;
            &:hover{
              color: grey;
            }
          }
        }
      }
    }
    .nav-button {
      display: none;
      margin-right: 5px;
      .nav-but {
        background: white;
        display: inline-block;
        padding: 2px;
        border: 1px solid $black;
        cursor: pointer;
        .nav-span {
          display: block;
          width: 35px;
          height: 6px;
          background: $black;
          margin-bottom: 2px;
          cursor: pointer;
        }
      }
    }
  }
  .info{
    grid-area: c;
    border-top: 2px solid $grey;
    border-bottom: 2px solid $grey;
    overflow: hidden;
    display: grid;
    color: black;
    justify-content: center;
    align-items: center;
    padding: 10px;
    .info-block{
      width: 480px;
      .title{
        font-size: 60px;
        font-family: "Gilroy-ExtraBold", sans-serif;
      }
      .adr{
        display: inline-flex;
        position: relative;
        text-transform: uppercase;
        font-size: 20px;
        padding: 20px 0;
        .adr-item1{
          display: flex;
          align-items: center;
          padding-right: 35px;
          font-family: 'Gilroy-Bold', sans-serif;
          &:before{
            content: '';
            background: url("../../img/sprite_.png") -56px 197px;
            width: 49px;
            height: 41px;
            display: inline-block;
          }
        }
        .adr-item2{
          font: {
            size: 20px;
            family: Gilroy-Medium, sans-serif;
          };
        }
      }
      .info-text{
        font: {
          size: 20px;
          family: Gilroy-Regular, sans-serif;;
        };
        padding-bottom: 25px;
      }
      .but1-item{
        margin-right: 30px;
      }
    }
  }
  .photo{
    background: url("../../img/photo.png") center;
    background-size: cover;
    grid-area: d;
  }
  .buy, .sell{
    grid-area: e;
    border-right: 2px solid $grey;
    border-bottom: 2px solid $grey;
    display: flex;
    justify-content: center;
    .block{
      @include block;
    }
  }
  .sell{
    grid-area: f;
    border-right: none;
  }
}

@media screen and (max-width: 1600px) {
  header{
    grid-template-rows: auto 425px 600px 240px;
    grid-template-columns: 16.6% 41.7% 41.7%;
    grid-template-areas:
            "a b b"
            "a c c"
            "a d d"
            "a e f";
  }
  header .info .info-block {
    width: auto;
  }
  header .name .ars {
    width: auto;
  }
}
@media screen and (max-width: 1060px){
  header{
    grid-template-rows: 0px auto auto 600px auto;
    grid-template-columns: 50% 50%;
    grid-template-areas:
                "b b"
                "a a"
                "c c"
                "d d"
                "e f";
  }
  header .info{
    border-top: none;
  }
  header .menu .nav-button{
    display: block;
    cursor: pointer;
    position: absolute;
    top: 15px;
    right: 0;
  }
  header .menu nav ul{
    left: 0;
    top:77px;
    text-align: center;
    width: 100%;
    display: block;
    max-height: 0;
    overflow: hidden;
    padding-top: 15px;
    margin-left: 0;
    li{
      display: flex;
      justify-content: center;
      margin-right: 0;
      a{
        color: white;
        text-align: center;
        padding: 10px 0;
        font-size: 40px;
        span{
          display: none;
        }

      }
      .active_{
        &:before{
          display: none;
        }
      }
    }
  }
  header .name{
    padding: 5px;
    border-right: none;
  }
  header .info .info-block {
    text-align: center;
  }
  header .name .icons{
    display: none;
  }
  header .menu{
    border-bottom: none;
  }
}
@media screen and (max-width: 576px){
  header{
    grid-template-rows: 0px auto auto 300px auto auto;
    grid-template-columns: 100%;
    grid-template-areas:
            "b"
            "a"
            "c"
            "d"
            "e"
            "f";
  }
  header .buy .block h2, header .sell .block h2  {
    font-size: 40px;
    padding: 0;
  }
  header .buy .block h4:nth-of-type(2), header .sell .block h4:nth-of-type(2) {
    padding-bottom: 5px;
  }
  header .info .info-block .but1-item {
    margin-right: 0px;
  }
  }

@media screen and (max-width: 380px){
  header .info .info-block .title {
    font-size: 50px;
  }
  header .info .info-block .info-text {
    font-size: 15px;
    padding-bottom: 15px;
  }
}























.fff{
  display: grid;
  grid-template-rows: 100px 100px 100px;
  grid-template-columns: 100px 100px 100px 100px;
  .item1{
    background: yellow;
    grid-row: 1/4;
  }
  .item2{
    background: blue;
    grid-column-start: 2;
    grid-column-end: 5;
  }
  .item3{
    background: gold;
    grid-column-start: 2;
    grid-column-end: 4;
  }
  .item4{
    background: green;
    grid-column-start: 4;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 4;
  }
  .item5{
    background: gray;
  }
  .item6{
    background: goldenrod;
  }
}


