
/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer{
    display: grid;
    font-family: "Gilroy-Light", sans-serif;
    grid-template-columns: 16.6% 14.3% 14.3% 54.8%;
    grid-template-rows: auto 540px 180px;
    grid-template-areas:
            "a b b b"
            "a c c d"
            "a e f d";
  .name{
    grid-area: a;
    border-right: 2px solid $grey;
    border-bottom: 2px solid $grey;
    text-align: center;
    justify-content: center;
    display: grid;
    padding: 35px 5px 30px 5px;
    .ars{
      width: 160px;
      display: inline-block;
      text-decoration: none;
      color: black;
      .title{
        font-size: 48px;
      }
      .name-text{
        font-size: 16px;
      }
    }

  }
    .footer-contact{
      grid-area: b;
      font-size: 36px;
      text-align: center;
      padding-top: 70px;
      padding-bottom: 40px;
      font-family: Gilroy-Bold, sans-serif;
    }
    .footer-address{
      grid-area: c;
      border-top: 2px solid $grey;
      border-bottom: 2px solid $grey;
      font-family: "Gilroy-Light", sans-serif;
      overflow: hidden;
      padding: 5px 5px 5px 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 18px;
      .address-block{
        strong{
          font-family: Gilroy-Bold, sans-serif;
          display: inline-block;
          padding-bottom: 35px;
          text-transform: uppercase;
          font-size: 20px;
          margin-left: -20px;
        }
        p:nth-of-type(1){
          display: flex;
          padding: 0;
          align-items: center;
          font-size: 18px;
          font-family: Gilroy-Medium, sans-serif;
          &:before{
            content: '';
            background: url(../../img/sprite2.png) -56px 199px;
            width: 49px;
            height: 43px;
            margin-left: -50px;
            display: inline-block;
          }
        }
        p:nth-of-type(2){
          display: flex;
          align-items: center;
          padding-bottom: 55px;
          font-size: 18px;
          font-family: Gilroy-Medium, sans-serif;
          &:before{
            content: '';
            background: url(../../img/sprite2.png) -374px 199px;
            width: 49px;
            height: 42px;
            margin-left: -50px;
            display: inline-block;
          }
        }
        p:nth-of-type(3){
          display: flex;
          align-items: center;
          font-size: 18px;
          font-family: Gilroy-Medium, sans-serif;
          &:before{
            content: '';
            background: url(../../img/sprite2.png) -56px 199px;
            width: 49px;
            height: 43px;
            margin-left: -50px;
            display: inline-block;
          }
        }
        p:nth-of-type(4){
          display: flex;
          align-items: center;
          padding-bottom: 35px;
          font-size: 18px;
          font-family: Gilroy-Medium, sans-serif;
          &:before{
            content: '';
            background: url(../../img/sprite2.png) -374px 199px;
            width: 49px;
            height: 42px;
            margin-left: -50px;
            display: inline-block;
          }
        }
      }

      .footer-button{
        margin-left: -50px;
      }
    }
    .footer-img{
      background: url("../../img/photo2.png") round;
      background-size: cover;
      grid-area: d;
    }
    .footer-soc{
      grid-area: e;
      border-right: 2px solid $grey;
      border-bottom: 2px solid $grey;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      padding: 10px;
      flex-direction: column;
      strong{
        font-size: 18px;
        padding-bottom: 10px;
        font-family: Gilroy-Bold, sans-serif;
        text-transform: uppercase;
      }
    }
    .footer-mail{
      grid-area: f;
      border-bottom: 2px solid $grey;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      font-size: 18px;
      strong{
        text-transform: uppercase;
        padding-bottom: 10px;
        font-family: Gilroy-Bold, sans-serif;
      }
      p{
        padding: 0;
        font-size: 18px;
        font-family: Gilroy-Medium, sans-serif;
      }
    }
}



@media screen and (max-width: 1600px) {
  footer {
    grid-template-columns: 16.6% 41.7% 41.7%;
    grid-template-rows: auto 480px auto;
    grid-template-areas: "a b b"
                         "a c d"
                         "a e f";
  }

}
@media screen and (max-width: 992px){
  footer {
    grid-template-columns: 30% 70%;
    grid-template-rows: auto auto 480px auto;
    grid-template-areas: "a a"
                         "b b"
                         "c d"
                         "e f";
  }
  footer .name .ars{
    width: 100%;
  }
  footer .name{
    border-right: none;
    border-bottom: none;
    padding: 5px;
  }
  footer .footer-contact {
    padding: 5px;
  }
}

@media screen and (max-width: 680px) {
  footer {
    grid-template-columns: 50% 50%;
    grid-template-rows: auto auto 480px auto auto;
    grid-template-areas:  "a a"
                          "b b"
                          "d d"
                          "c c"
                          "e f";
  }
  footer .footer-address{
    border-top: none;
  }
  footer .footer-address .address-block p{
    padding: 5px;
  }
  footer .footer-address .address-block p:nth-of-type(2){
    padding: 5px;
  }
  footer .footer-address .address-block p:nth-of-type(4){
    padding-bottom: 5px;
  }
}

@media screen and (max-width: 480px) {
	footer{
      grid-template-rows: auto auto 250px auto auto;
    }
  footer .footer-soc .icons{
    grid-template-columns: 35px 35px 35px;
  }
}